import { createSlice } from 'store/core/@reduxjs/toolkit';
export interface ExamState {
  error?: any | null;
  loading: boolean;
  success: any;
  exams: any;
}

export const initialState: ExamState = {
  loading: false,
  error: null,
  success: {},
  exams: {},
};

const ExaminationSlice = createSlice({
  name: 'examination',
  initialState,
  reducers: {
    getExams(state, action) {
      state.loading = true;
    },

    getExamsSuccess(state, action) {
      state.loading = false;
      state.exams = action.payload;
    },

    getExamsFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    exportExams(state, action) {
      state.loading = true;
    },

    exportExamsSuccess(state, action) {
      state.loading = false;
    },

    exportExamsFail(state, action) {
      state.loading = false;
    },
  },
});

export const { actions, reducer, name: sliceKey } = ExaminationSlice;
