/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSelector } from '@reduxjs/toolkit';
import { RootState } from '../../core/types';
import { initialState } from './slice';

const selectSettingState = (state: RootState) =>
  state.autoPayment || initialState;

export const selectError = createSelector(
  [selectSettingState],
  autoPaymentState => autoPaymentState.error,
);

export const selectSuccess = createSelector(
  [selectSettingState],
  autoPaymentState => autoPaymentState.success,
);

export const selectLoading = createSelector(
  [selectSettingState],
  autoPaymentState => autoPaymentState.loading,
);

export const selectHistories = createSelector(
  [selectSettingState],
  autoPaymentState => autoPaymentState.histories,
);

export const selectFileExcell = createSelector(
  [selectSettingState],
  autoPaymentState => autoPaymentState.filesExcell,
);

export const selectLogUpdateTime = createSelector(
  [selectSettingState],
  autoPaymentState => autoPaymentState.logUpdateTime,
);
