import React from 'react';
import * as paymentSlice from 'store/payment/shared/slice';
import * as fromPayment from 'store/payment/shared/selectors';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { PaymentSaga } from 'store/payment/shared/saga';
import { useForm, Controller } from 'react-hook-form';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import { TekDatePicker } from 'app/components/InputDatePicker';
import { DEFAULT_FORMAT_DATE } from 'store/common/constants/common.constant';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import AppLoading from 'app/components/Loading';
import DateFnsUtils from '@date-io/date-fns';
import viLocale from 'date-fns/locale/vi';

interface Props {
  open: boolean;
  title: string;
  onClose: () => void;
}

export function ModalSyncPaymentPending({ open, onClose, title }: Props) {
  useInjectReducer({
    key: paymentSlice.sliceKey,
    reducer: paymentSlice.reducer,
  });
  useInjectSaga({
    key: paymentSlice.sliceKey,
    saga: PaymentSaga,
  });
  const loading: any = useSelector(fromPayment.selectLoading);
  const { control, handleSubmit, setValue, errors } = useForm<any>();
  const dispatch = useDispatch();

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'md'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
      disableBackdropClick
    >
      <DialogContent>
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={viLocale}>
          <form
            onSubmit={handleSubmit(values => {
              dispatch(paymentSlice.actions.syncPaymentsPending(values));
            })}
            className="row"
          >
            <div className="form-group col-md-12">
              <h4 className="m-0">{title}</h4>
            </div>
            <div className="col-md-4 form-group">
              <label>Ngày bắt đầu</label>
              <Controller
                name="fromDate"
                control={control}
                defaultValue={null}
                rules={{ required: true }}
                render={({ field, ...input }: any) => {
                  return (
                    <DatePicker
                      value={input.value}
                      format={DEFAULT_FORMAT_DATE}
                      TextFieldComponent={TekDatePicker}
                      placeholder="Ngày bắt đầu"
                      onChange={val => {
                        setValue('fromDate', val);
                      }}
                    />
                  );
                }}
              />
              {errors['fromDate'] &&
                errors['fromDate']?.type === 'required' && (
                  <div className="invalid-feedback d-block">
                    Vui lòng nhập ngày bắt đầu
                  </div>
                )}
            </div>
            <div className="col-md-4 form-group">
              <label>Ngày kết thúc</label>
              <Controller
                name="toDate"
                control={control}
                defaultValue={null}
                rules={{ required: true }}
                render={({ field, ...input }: any) => {
                  return (
                    <DatePicker
                      value={input.value}
                      format={DEFAULT_FORMAT_DATE}
                      TextFieldComponent={TekDatePicker}
                      maxDate={new Date()}
                      placeholder="Ngày kết thúc"
                      onChange={val => {
                        setValue('toDate', val);
                      }}
                    />
                  );
                }}
              />
              {errors['toDate'] && errors['toDate']?.type === 'required' && (
                <div className="invalid-feedback d-block">
                  Vui lòng nhập ngày kết thúc
                </div>
              )}
            </div>
            <div className="col-md-4 form-group">
              <label className="t-hidden">Đồng bộ</label>
              <div className="btn-group d-flex">
                <button className="btn btn-success btn__export" type="submit">
                  Đồng bộ <i className="fa fa-refresh"></i>
                </button>
                <button
                  className="btn btn-warning btn__export ml-2"
                  type="button"
                  onClick={onClose}
                >
                  Đóng
                </button>
              </div>
            </div>
          </form>
          {loading ? <AppLoading loading={true} /> : null}
        </MuiPickersUtilsProvider>
      </DialogContent>
    </Dialog>
  );
}
