import { call, put, takeLatest, all } from 'redux-saga/effects';
import { configResponse } from 'utils/request';
import { ExaminationHttp } from '../services/examination.http';
import { actions } from '../shared/slice';
import { get } from 'lodash';
const examRequest = new ExaminationHttp();

export function* getExams(api, action) {
  const response = yield call(api.getExams, action.payload);
  try {
    const data: any = yield configResponse(response);
    yield put(actions.getExamsSuccess(data));
  } catch (error) {
    yield put(actions.getExamsFail(get(error, 'message')));
  }
}

export function* exportExams(api, action) {
  const response = yield call(api.exportExams, action.payload);
  try {
    const data: any = yield configResponse(response);
    yield put(actions.exportExamsSuccess(data));
  } catch (error) {
    yield put(actions.exportExamsFail(get(error, 'message')));
  }
}

export function* ExamSaga() {
  yield all([yield takeLatest(actions.getExams.type, getExams, examRequest)]);
  yield all([
    yield takeLatest(actions.exportExams.type, exportExams, examRequest),
  ]);
}
