import { HttpRequest, configRequest } from 'utils/request';
import { ApiRoutes } from '../constants';
import { APP_CONSTANT } from 'store/common/constants/common.constant';

export class PaymentHttp {
  request: any;
  endPoint: any;
  constructor(endPoint = APP_CONSTANT.API.ENDPOINT) {
    this.request = new HttpRequest(endPoint).request;
    this.endPoint = endPoint;
  }

  public getPayments = (filter: any): Promise<any> => {
    return this.request.get(ApiRoutes.GET_PAYMENTS, configRequest(filter));
  };

  public syncPaymentsPending = (filter: any): Promise<any> => {
    return this.request.get(ApiRoutes.PAYMENT_SYNC, configRequest(filter));
  };

  public getPaymentsPending = (filter: any): Promise<any> => {
    return this.request.get(
      ApiRoutes.GET_PAYMENTS_PENDING,
      configRequest(filter),
    );
  };
}
