import { call, put, takeLatest, all } from 'redux-saga/effects';
import { configResponse } from 'utils/request';
import { PaymentHttp } from '../services/payment.http';
import { actions } from '../shared/slice';
import { get } from 'lodash';
const paymentHttp = new PaymentHttp();

export function* getPayments(api, action) {
  const response = yield call(api.getPayments, action.payload);
  try {
    const data: any = yield configResponse(response);
    yield put(actions.getPaymentsSuccess(data));
  } catch (error) {
    yield put(actions.getPaymentsFail(get(error, 'message')));
  }
}

export function* getPaymentsPending(api, action) {
  const response = yield call(api.getPaymentsPending, action.payload);
  try {
    const data: any = yield configResponse(response);
    yield put(actions.getPaymentsPendingSuccess(data));
  } catch (error) {
    yield put(actions.getPaymentsPendingFail(get(error, 'message')));
  }
}

export function* syncPaymentsPending(api, action) {
  const response = yield call(api.syncPaymentsPending, action.payload);
  try {
    const data: any = yield configResponse(response);
    yield put(actions.syncPaymentsPendingSuccess(data));
  } catch (error) {
    yield put(actions.syncPaymentsPendingFail(get(error, 'message')));
  }
}

export function* PaymentSaga() {
  yield all([
    yield takeLatest(actions.getPayments.type, getPayments, paymentHttp),
    yield takeLatest(
      actions.syncPaymentsPending.type,
      syncPaymentsPending,
      paymentHttp,
    ),
    yield takeLatest(
      actions.getPaymentsPending.type,
      getPaymentsPending,
      paymentHttp,
    ),
  ]);
}
