import { HttpRequest } from 'utils/request';
import { APP_CONSTANT } from '../../common/constants/common.constant';
import { AutoPaymentApiRoutes } from '../constants/autopayment.constant';

export class AutoPaymentHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.PAYMENT) {
    this.request = new HttpRequest(endPoint).request;
  }

  public updateTime = (data: any): Promise<any> => {
    return this.request.post(AutoPaymentApiRoutes.UPDATE_TIME, data);
  };

  public renderUpdateTime = ({
    date,
    status,
    page_size,
    page_number,
    employee_name,
  }): Promise<any> => {
    return this.request.get(
      AutoPaymentApiRoutes.RENDER_LOG_TIME +
        `?input_date=${date}&status_input=${status}&employee_name=${employee_name}&page_number=${page_number}&page_size=${page_size}`,
    );
  };

  public paidWaittingHistories = ({
    date,
    status,
    page_size,
    page_number,
    employee_name,
  }): Promise<any> => {
    return this.request.get(
      AutoPaymentApiRoutes.PAID_WAITTING_HISTORIES +
        `?input_date=${date}&status_input=${status}&employee_name=${employee_name}&page_number=${page_number}&page_size=${page_size}`,
    );
  };

  public exportWaittingHistories = ({
    date,
    status,
    page_size,
    page_number,
    employee_name,
  }): Promise<any> => {
    return this.request.get(
      AutoPaymentApiRoutes.EXPORT_WAITTING_HISTORIES +
        `?input_date=${date}&status_input=${status}&employee_name=${employee_name}&page_number=${page_number}&page_size=${page_size}`,
    );
  };
}
