/* eslint-disable @typescript-eslint/no-use-before-define */
/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable react-hooks/exhaustive-deps */
//eslint-disable-next-line
import React, { useEffect, useState } from 'react';
import * as _ from 'lodash';
import * as examSlice from 'store/examination/shared/slice';
import * as fromExam from 'store/examination/shared/selectors';
import * as RoomSelector from 'store/room/shared/selectors';
import * as RoomSlice from 'store/room/shared/slice';
import * as AuthSelector from 'store/auth/shared/selectors';
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
  Tooltip,
} from '@material-ui/core';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import { useStyles } from 'store/common/constants/common.constant';
import { RESPONSE_MESSAGE } from 'store/examination/constants';
import { Order } from 'utils/app.helper';
import { CssTextField } from 'store/room/constants/http.constant';
import {
  DEFAULT_FORMAT_DATE,
  DEFAULT_FORMAT_DATE_TIME,
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { useInjectReducer, useInjectSaga } from 'redux-injectors';
import { useDispatch, useSelector } from 'react-redux';
import { ExamSaga } from 'store/examination/shared/saga';
import { AppHelper } from 'utils/app.helper';
import { useForm, Controller } from 'react-hook-form';
import { TekDatePicker } from 'app/components/InputDatePicker';
import { RoomSaga } from 'store/room/shared/saga';
import { useSnackbar } from 'notistack';
import { NotifyService } from 'services/notify.service';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import { defaultPagi, defaultDate } from './Patient';
import TableHeaderSor from 'app/components/TableHeaderSort';
import AppLoading from 'app/components/Loading';
import DateFnsUtils from '@date-io/date-fns';
import viLocale from 'date-fns/locale/vi';
import Autocomplete from '@material-ui/lab/Autocomplete';

export const colsExam = [
  { id: 'no', label: '#', minWidth: 100 },
  { id: 'patient_code', label: 'Mã bệnh nhân', minWidth: 200 },
  { id: 'patient_name', label: 'Tên bệnh nhân', minWidth: 200 },
  { id: 'gender', label: 'Giới tính', minWidth: 150 },
  { id: 'patient_type', label: 'Loại hình khám', minWidth: 250 },
  { id: 'created_date', label: 'Ngày đăng ký', minWidth: 300 },
  { id: 'registered_date', label: 'Ngày tái khám', minWidth: 300 },
  { id: 'department_name', label: 'Phòng khám', minWidth: 350 },
  { id: 'is_have_clinic', label: ' CLS lần sau', minWidth: 200 },
];

export function Examination() {
  useInjectReducer({ key: RoomSlice.sliceKey, reducer: RoomSlice.reducer });
  useInjectSaga({ key: RoomSlice.sliceKey, saga: RoomSaga });
  useInjectReducer({
    key: examSlice.sliceKey,
    reducer: examSlice.reducer,
  });
  useInjectSaga({
    key: examSlice.sliceKey,
    saga: ExamSaga,
  });
  const classes = useStyles();
  const dateUtil = new DateFnsUtils();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const dataRoomAll: any = useSelector<any>(RoomSelector.selectRooms);
  const users: any = useSelector<any>(AuthSelector.selectUser);
  const exams: any = useSelector(fromExam.selectExams);
  const loading: any = useSelector(fromExam.selectLoading);
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<any>('patient_code');
  const [pagi, setPagi] = useState<any>(defaultPagi);
  const [startDate, setStartDate] = useState<any>(defaultDate.startDate);
  const [endDate, setEndDate] = useState<any>(defaultDate.endDate);
  const { handleSubmit, register, control, setValue, getValues } = useForm<any>(
    {
      defaultValues: {
        ...defaultDate,
      },
    },
  );

  useEffect(() => {
    dispatch(RoomSlice.actions.getRoomAll());
    dispatch(examSlice.actions.getExams({ ...getFormValues() }));
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case examSlice.actions.exportExamsSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.EXPORT_EXAM_SUCCESS)
            .setSuccessNotify();
          AppHelper.createDownloadFile({
            file_path: payload.file_path,
            file_name: payload.file_name,
          });
          break;
        case examSlice.actions.exportExamsFail.type:
          notifyController.setNotifyMessage(payload).setFailNotify();
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
    };
  }, []);

  const getFormValues = () => {
    return {
      search: getValues('search'),
      startDate: getValues('startDate'),
      endDate: getValues('endDate'),
      employeeName: users.full_name,
      employeeId: users.id,
      pageNumber: pagi.pageNumber,
      pageSize: pagi.pageSize,
    };
  };

  const handleReset = () => {
    setValue('search', null);
    setValue('startDate', new Date());
    setValue('endDate', new Date());
    setStartDate(new Date());
    setEndDate(new Date());
    setPagi(defaultPagi);
    dispatch(
      examSlice.actions.getExams({
        ...getFormValues(),
        startDate: new Date(),
        endDate: new Date(),
        pageNumber: 1,
        pageSize: 10,
      }),
    );
  };

  const handleRequestSort = (_e, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleChangePage = (e: unknown, pageNumber: number) => {
    let newPagi = Object.assign({}, pagi, {
      pageNumber: pageNumber + 1,
    });
    setPagi(newPagi);
    dispatch(examSlice.actions.getExams({ ...getFormValues(), ...newPagi }));
  };

  const handleChangeRowPerPage = event => {
    let newPagi = Object.assign({}, pagi, {
      pageSize: +event.target.value,
      pageNumber: 1,
    });
    setPagi(newPagi);
    dispatch(examSlice.actions.getExams({ ...getFormValues(), ...newPagi }));
  };

  const handleExportExcel = () => {
    dispatch(examSlice.actions.exportExams({ ...getFormValues() }));
  };

  const submitSearch = value => {
    dispatch(examSlice.actions.getExams({ ...getFormValues() }));
  };

  const renderNavControl = () => {
    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={viLocale}>
        <form onSubmit={handleSubmit(submitSearch)}>
          <div className="row" style={{ justifyContent: 'space-between' }}>
            <div className="col-md-3" hidden>
              <label>Phòng khám</label>
              <Autocomplete
                inputValue={pagi.deptDesc}
                options={
                  !_.isEmpty(dataRoomAll) && dataRoomAll.length
                    ? dataRoomAll
                    : []
                }
                getOptionLabel={(option: any) =>
                  option.description + ' - ' + option.code
                }
                renderInput={(params: any) => {
                  let inputProps = {
                    ...params.inputProps,
                    value: pagi.deptDesc,
                  };
                  return (
                    <CssTextField
                      style={{
                        background: 'white',
                        fontSize: '16px',
                      }}
                      {...params}
                      size="small"
                      variant="outlined"
                      name="room"
                      inputProps={{
                        ...inputProps,
                      }}
                      placeholder="Nhập tên phòng khám"
                    />
                  );
                }}
              />
            </div>
            <div className="col-md-2">
              <label>Tìm bệnh nhân</label>
              <input
                className="form-control"
                ref={register()}
                placeholder="Tìm bệnh nhân"
                type="text"
                name="search"
              />
            </div>
            <div className="col-md-4 row">
              <div className="col-md-5">
                <label>Thời gian</label>
                <Controller
                  name="startDate"
                  control={control}
                  defaultValue={null}
                  render={({ field, ...input }: any) => {
                    return (
                      <DatePicker
                        value={input.value}
                        format={DEFAULT_FORMAT_DATE}
                        TextFieldComponent={TekDatePicker}
                        placeholder="Ngày bắt đầu"
                        maxDate={endDate}
                        onChange={val => {
                          setValue('startDate', val);
                          setStartDate(val);
                        }}
                      />
                    );
                  }}
                />
              </div>
              <span className="span__line">-</span>
              <div className="col-md-5 mg-t">
                <Controller
                  name="endDate"
                  control={control}
                  defaultValue={null}
                  render={({ field, ...input }: any) => {
                    return (
                      <DatePicker
                        value={input.value}
                        format={DEFAULT_FORMAT_DATE}
                        TextFieldComponent={TekDatePicker}
                        placeholder="Ngày kết thúc"
                        minDate={startDate}
                        maxDate={new Date()}
                        onChange={val => {
                          setValue('endDate', val);
                          setEndDate(val);
                        }}
                      />
                    );
                  }}
                />
              </div>
            </div>
            <div className="col-md-2">
              <div className="btn-group">
                <button className="btn btn-primary btn__search" type="submit">
                  Tìm kiếm <i className="fa fa-search"></i>
                </button>
              </div>
            </div>
            <div className="col-md-4 export__examination text-right">
              <div className="btn-group mr-2">
                <button
                  type="button"
                  className="btn btn-warning btn__export"
                  onClick={handleReset}
                >
                  Reset <i className="fa fa-refresh"></i>
                </button>
              </div>
              <div className="btn-group">
                <button
                  disabled={_.isEmpty(exams) || !exams.data.length}
                  style={{
                    cursor:
                      _.isEmpty(exams) || !exams.data.length
                        ? 'default'
                        : 'pointer',
                  }}
                  type="button"
                  className="btn btn-success btn__export"
                  onClick={handleExportExcel}
                >
                  Xuất file <i className="fa fa-download"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
      </MuiPickersUtilsProvider>
    );
  };

  const renderColumns = () => (
    <TableHeaderSor
      classes={classes}
      onRequestSort={handleRequestSort}
      order={order}
      orderBy={orderBy}
      columns={colsExam}
    />
  );

  const renderTable = () => {
    if (!_.isEmpty(exams) && exams.data.length) {
      const tableData: any[] = AppHelper.stableSort(
        exams.data,
        AppHelper.getComparator(order, orderBy),
      );
      return tableData.map((row, index) => {
        return (
          <TableRow key={index} hover>
            {colsExam.map(column => {
              const colId = column.id;
              const value = row[colId];
              if (colId === 'no') {
                const { page_number, page_size } = exams;
                return (
                  <TableCell key={colId} className={classes.td}>
                    {page_number === 1
                      ? page_number + index
                      : index + 1 + (page_number - 1) * page_size}
                  </TableCell>
                );
              }
              if (colId === 'created_date') {
                return (
                  <TableCell key={colId} className={classes.td}>
                    {value
                      ? dateUtil.format(
                          new Date(value),
                          DEFAULT_FORMAT_DATE_TIME,
                        )
                      : '-'}
                  </TableCell>
                );
              }
              if (colId === 'registered_date') {
                return (
                  <TableCell key={colId} className={classes.td}>
                    {value
                      ? dateUtil.format(new Date(value), DEFAULT_FORMAT_DATE)
                      : '-'}
                  </TableCell>
                );
              }
              if (colId === 'patient_type') {
                return (
                  <TableCell key={colId} className={classes.td}>
                    {value === 'VP' ? 'Dịch Vụ' : value}
                  </TableCell>
                );
              }
              if (colId === 'is_have_clinic') {
                return (
                  <TableCell key={colId} className={classes.td}>
                    <Tooltip
                      title={`Trạng thái ${
                        +value ? 'kích hoạt' : 'chưa kích hoạt'
                      }`}
                      enterDelay={500}
                      leaveDelay={200}
                    >
                      <i
                        style={{
                          color: +value ? '#4caf50' : '#ff0000',
                          fontSize: '16px',
                        }}
                        className={`fa fa-${+value ? 'check' : 'times'}-circle`}
                        aria-hidden="true"
                      ></i>
                    </Tooltip>
                  </TableCell>
                );
              }
              return (
                <TableCell key={colId} className={classes.td}>
                  {value ? value : '-'}
                </TableCell>
              );
            })}
          </TableRow>
        );
      });
    }
    return (
      <TableRow>
        <TableCell className="w-100" align="center" colSpan={colsExam.length}>
          {EMPTY_MESSAGE}
        </TableCell>
      </TableRow>
    );
  };

  const renderPagination = () => {
    if (!_.isEmpty(exams) && exams.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={exams.total_records}
          rowsPerPage={pagi.pageSize}
          page={pagi.pageNumber - 1}
          labelRowsPerPage="Hiển thị"
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">
          <ul className="nav customtab nav-tabs" role="tablist"></ul>
          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-topline-red">
                    <div className="card-head">
                      <div className="tools"></div>
                    </div>
                    <div className="card-body">
                      {renderNavControl()}
                      <Paper className={classes.root}>
                        {loading ? <AppLoading loading={true} /> : null}
                        <TableContainer className={classes.container}>
                          <Table stickyHeader aria-label="sticky table">
                            <TableHead>{renderColumns()}</TableHead>
                            <TableBody>{renderTable()}</TableBody>
                          </Table>
                        </TableContainer>
                        {renderPagination()}
                      </Paper>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
