import { HttpRequest, configRequest } from 'utils/request';
import { ApiRoutes } from '../constants';
import { APP_CONSTANT } from 'store/common/constants/common.constant';

export class ExaminationHttp {
  request: any;
  endPoint: any;
  constructor(endPoint = APP_CONSTANT.API.ENDPOINT) {
    this.request = new HttpRequest(endPoint).request;
    this.endPoint = endPoint;
  }

  public getExams = (filter: any): Promise<any> => {
    return this.request.get(ApiRoutes.EXAMS, configRequest(filter));
  };

  public exportExams = (filter: any): Promise<any> => {
    return this.request.get(ApiRoutes.EXAMS_EXPORT, configRequest(filter));
  };
}
