/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSlice } from '../../../store/core/@reduxjs/toolkit';
import { AppHelper } from 'utils/app.helper';
import {
  RESPONSE_AUTO_PAY_CONSTANT,
  RESPONSE_AUTO_PAY_MESSAGE,
} from '../constants/autopayment.constant';

export interface AutoPaymentState {
  loading?: boolean;
  success?: any;
  error?: any;
  histories?: any;
  logUpdateTime: any;
  filesExcell: any;
}

export const initialState: AutoPaymentState = {
  loading: false,
  success: null,
  error: null,
  histories: {},
  logUpdateTime: {},
  filesExcell: {},
};

const AutoPaymentSlice = createSlice({
  name: 'autoPayment',
  initialState,
  reducers: {
    updateTime(state, action) {
      state.loading = true;
    },

    updateTimeSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_AUTO_PAY_CONSTANT.UPDATE_TIME_SUCCESS,
        message: RESPONSE_AUTO_PAY_MESSAGE.UPDATE_TIME_SUCCESS,
      };
    },

    updateTimeFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_AUTO_PAY_CONSTANT.UPDATE_TIME_FAIL,
        message: action.payload,
      };
    },

    renderUpdateTime(state, action) {
      state.loading = true;
    },

    renderUpdateTimeSuccess(state, action) {
      state.loading = false;
      state.logUpdateTime = action.payload;
      state.success = {
        key: RESPONSE_AUTO_PAY_CONSTANT.RENDER_LOG_TIME_SUCCESS,
        message: RESPONSE_AUTO_PAY_MESSAGE.UPDATE_TIME_SUCCESS,
      };
    },

    renderUpdateTimeFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_AUTO_PAY_CONSTANT.RENDER_LOG_TIME_FAIL,
        message: action.payload,
      };
    },

    paidWaittingHistories(state, action) {
      state.loading = true;
    },

    paidWaittingHistoriesSuccess(state, action) {
      state.loading = false;
      state.histories = action.payload;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_AUTO_PAY_CONSTANT.PAID_WAITTING_HISTORIES_SUCCESS,
        message: RESPONSE_AUTO_PAY_MESSAGE.PAID_WAITTING_HISTORIES_SUCCESS,
      };
    },

    paidWaittingHistoriesFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_AUTO_PAY_CONSTANT.PAID_WAITTING_HISTORIES_FAIL,
        message: action.payload,
      };
    },

    exportWaittingHistories(state, action) {
      state.loading = true;
    },

    exportWaittingHistoriesSuccess(state, action) {
      state.loading = false;
      state.filesExcell = action.payload;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_AUTO_PAY_CONSTANT.EXPORT_WAITTING_HISTORIES_SUCCESS,
        message: RESPONSE_AUTO_PAY_MESSAGE.EXPORT_WAITTING_HISTORIES_SUCCESS,
      };
    },

    exportWaittingHistoriesFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_AUTO_PAY_CONSTANT.EXPORT_WAITTING_HISTORIES_FAIL,
        message: action.payload,
      };
    },

    setError(state, action) {
      state.error = action.payload;
    },

    setSuccess(state, action) {
      state.success = action.payload;
    },

    clearError(state) {
      state.error = {};
    },

    clearSuccess(state) {
      state.success = {};
    },

    clearFileExcell(state) {
      state.filesExcell = {};
    },

    resetAll(state) {
      state.loading = false;
      state.success = null;
      state.error = null;
    },
  },
});

export const { actions, reducer, name: sliceKey } = AutoPaymentSlice;
