/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect, useMemo } from 'react';
import * as fromPatientSelector from 'store/patient/shared/selectors';
import * as _ from 'lodash';
import * as patientSlice from 'store/patient/shared/slice';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { PatientSaga } from 'store/patient/shared/saga';
import { NotifyService } from 'services/notify.service';
import { PatientService } from 'services/patient.service';
import { CurrencyService } from 'services/currency.service';
import { useSnackbar } from 'notistack';
import { AppHelper, Order } from 'utils/app.helper';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import viLocale from 'date-fns/locale/vi';
import { selectIsAuthenticated, selectUser } from 'store/auth/shared/selectors';
import {
  DEFAULT_FORMAT_DATE,
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import DateFnsUtils from '@date-io/date-fns';
import AppLoading from 'app/components/Loading';
import TableHeaderSort from 'app/components/TableHeaderSort';
import { useStyles } from 'store/common/constants/common.constant';
import { TekDatePicker } from 'app/components/InputDatePicker';

export const balanceCols = [
  { id: 'no', label: '#', minWidth: 50 },
  { id: 'tekmedi_card_number', label: 'Mã thẻ', minWidth: 150 },
  { id: 'code', label: 'Mã bệnh nhân', minWidth: 150 },
  { id: 'full_name', label: 'Họ và tên', minWidth: 200 },
  {
    id: 'gender',
    label: 'Giới tính',
    minWidth: 130,
  },
  {
    id: 'birthday',
    label: 'Năm sinh',
    minWidth: 120,
  },
  {
    id: 'identity_card_number',
    label: 'CMND',
    minWidth: 100,
  },
  {
    id: 'phone',
    label: 'Số điện thoại',
    minWidth: 150,
  },
  {
    id: 'top_up_amount',
    label: 'Số tiền đã nạp',
    minWidth: 170,
  },
  {
    id: 'payment_amount',
    label: 'Số tiền đã trừ',
    minWidth: 170,
  },
  {
    id: 'balance',
    label: 'Số tiền trong thẻ',
    minWidth: 170,
  },
];

export function BalancePage() {
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  useInjectReducer({
    key: patientSlice.sliceKey,
    reducer: patientSlice.reducer,
  });
  useInjectSaga({ key: patientSlice.sliceKey, saga: PatientSaga });
  const classes = useStyles();
  const listPatientBalance = useSelector(
    fromPatientSelector.selectListPatientBalance,
  );
  const loading = useSelector(fromPatientSelector.selectLoading);
  const success = useSelector(fromPatientSelector.selectSuccess);
  const user: any = useSelector<any>(selectUser);
  const isAuthenticated: any = useSelector<any>(selectIsAuthenticated);
  const dispatch = useDispatch();
  const dateUtil = new DateFnsUtils();
  const [startDate, setStartDate] = useState<Date>(new Date());
  const [endDate, setEndDate] = useState<Date>(new Date());
  const [isCloseTable, setIsCloseTable] = useState(false);
  const [isOpenDisplay, setIsOpenDisplay] = useState(false);
  const [isRefresh, setIsRefresh] = useState<boolean>(false);

  const memoPatientBalance: any = useMemo(() => {
    const { data = [], recordsTotal } = listPatientBalance;
    return { data, recordsTotal };
  }, [listPatientBalance]);

  const [filterPatientBalance, setFilterPatientBalance] = useState<any>({
    startDate: new Date(),
    endDate: new Date(),
    page: 0,
    rowsPerPage: 10,
    userId: user.id,
    userName: user.user_name,
  });

  useEffect(() => {
    return () => {
      dispatch(patientSlice.actions.resetAll());
    };
  }, []);

  useEffect(() => {
    if (!_.isEmpty(success)) {
      notifyController.setNotifyMessage(success.message).setSuccessNotify();
      AppHelper.createDownloadFile({
        file_path: success.data.file_path,
        file_name: success.data.file_name,
      });
      dispatch(patientSlice.actions.clearSuccess());
    }
  }, [success]);

  useEffect(() => {
    if (isAuthenticated) {
      const initFilter = filterPatientBalance;
      setFilterPatientBalance({
        ...initFilter,
        userId: user.id,
        userName: user.user_name,
      });
    }
  }, [isAuthenticated]);

  useEffect(() => {
    if (_.get(filterPatientBalance, 'userId')) {
      dispatch(patientSlice.actions.getPatientBalance(filterPatientBalance));
    }
  }, [filterPatientBalance]);

  useEffect(() => {
    if (isRefresh) {
      dispatch(patientSlice.actions.getPatientBalance(filterPatientBalance));
      setIsRefresh(false);
    }
  }, [isRefresh]);

  const changeStartDate = date => {
    setStartDate(new Date(date));
  };

  const setNewFilter = newFilter => {
    setFilterPatientBalance(newFilter);
    dispatch(patientSlice.actions.getPatientBalance(newFilter));
  };

  const changeEndDate = date => {
    setEndDate(new Date(date));
  };

  const handleChangePage = (event: unknown, page: number) => {
    let changePage = Object.assign({}, filterPatientBalance, { page });
    setNewFilter(changePage);
  };

  const handleChangeRowsPerPage = event => {
    let changeRowPerPage = Object.assign({}, filterPatientBalance, {
      rowsPerPage: +event.target.value,
      page: 0,
    });
    setNewFilter(changeRowPerPage);
  };

  const submitSearch = e => {
    e.preventDefault();
    setFilterPatientBalance({
      ..._.cloneDeep(filterPatientBalance),
      startDate,
      endDate,
    });
  };

  const exportListPatientBalance = () => {
    dispatch(
      patientSlice.actions.exportPatientBalance({
        startDate,
        endDate,
        userName: user.user_name,
        userId: user.id,
      }),
    );
  };

  const validateData = () =>
    listPatientBalance.data && listPatientBalance.data.length ? true : false;

  const [order, setOrder] = React.useState<Order>('asc');
  const [orderBy, setOrderBy] = React.useState<any>('tekmedi_card_number');

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleIsOpenDisplay = () => {
    setIsOpenDisplay(!isOpenDisplay);
  };

  const handleCloseTable = () => {
    setIsCloseTable(!isCloseTable);
  };

  const handleRefresh = () => {
    setFilterPatientBalance({
      startDate: new Date(),
      endDate: new Date(),
      page: 0,
      rowsPerPage: 10,
      userId: user.id,
      userName: user.user_name,
    });
    setIsRefresh(true);
  };
  const renderNavControl = () => {
    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={viLocale}>
        <div className="row">
          <div className="col-md-6">
            <form className="row" onSubmit={submitSearch}>
              <div className="col-ms-12 col-md-4">
                <label>Thời gian</label>
                <DatePicker
                  value={startDate}
                  onChange={changeStartDate}
                  format={DEFAULT_FORMAT_DATE}
                  TextFieldComponent={TekDatePicker}
                  maxDate={endDate}
                />
              </div>
              <span className="span__line">-</span>
              <div className="col-ms-12 col-md-4 mg-t">
                <DatePicker
                  value={endDate}
                  onChange={changeEndDate}
                  format={DEFAULT_FORMAT_DATE}
                  TextFieldComponent={TekDatePicker}
                  minDate={startDate}
                  maxDate={new Date()}
                />
              </div>
              <div className="col-md-2">
                <div className="btn-group">
                  <button
                    className="btn btn-primary mr-3 btn__search"
                    type="submit"
                  >
                    Tìm kiếm <i className="fa fa-search"></i>
                  </button>
                </div>
              </div>
            </form>
          </div>
          <div className="col-md-2 export__examination">
            <div className="btn-group">
              <button
                className="btn btn-success btn__export"
                onClick={exportListPatientBalance}
              >
                Xuất file <i className="fa fa-upload"></i>
              </button>
            </div>
          </div>
        </div>
      </MuiPickersUtilsProvider>
    );
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={balanceCols}
      />
    );
  };

  const renderTable = () => {
    if (validateData()) {
      const tableData: any[] = AppHelper.stableSort(
        memoPatientBalance.data,
        AppHelper.getComparator(order, orderBy),
      );
      return tableData
        .map(balance => {
          const topupBalance = _.get(balance, 'top_up_amount');
          const topup = topupBalance && !isNaN(topupBalance) ? topupBalance : 0;
          const paymentBalance = _.get(balance, 'payment_amount');
          const payment =
            paymentBalance && !isNaN(paymentBalance) ? paymentBalance : 0;
          return {
            ...balance,
            balance: +topup - payment,
          };
        })
        .map((row, idx) => {
          return (
            <TableRow key={row.id} hover>
              {balanceCols.map(column => {
                const value = row[column.id];
                if (column.id === 'no') {
                  const { page, rowsPerPage } = filterPatientBalance;
                  return (
                    <TableCell key={column.id} className={classes.td}>
                      {page * rowsPerPage + idx + 1}
                    </TableCell>
                  );
                }
                if (column.id === 'birthday') {
                  return (
                    <TableCell key={column.id} className={classes.td}>
                      {value ? new PatientService().showBirthDay(row) : '-'}
                    </TableCell>
                  );
                }
                if (
                  column.id === 'payment_amount' ||
                  column.id === 'top_up_amount' ||
                  column.id === 'balance'
                ) {
                  return (
                    <TableCell
                      key={column.id}
                      align="center"
                      className={classes.td}
                    >
                      {CurrencyService.formatCurrency(value)}
                    </TableCell>
                  );
                }
                return (
                  <TableCell key={column.id} className={classes.td}>
                    {value ? value : '-'}
                  </TableCell>
                );
              })}
            </TableRow>
          );
        });
    } else {
      return (
        <TableRow>
          <TableCell
            className={classes.td + 'w-100'}
            align="center"
            colSpan={balanceCols.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
  };

  const renderPagination = () => {
    if (validateData()) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={memoPatientBalance.recordsTotal}
          rowsPerPage={
            filterPatientBalance ? filterPatientBalance.rowsPerPage : 10
          }
          page={filterPatientBalance ? filterPatientBalance.page : 0}
          labelRowsPerPage={'Hiển Thị'}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row" style={{ display: isCloseTable ? 'none' : 'block' }}>
      <div className="col-md-12">
        <div className="tabbable-line">
          <ul className="nav customtab nav-tabs" role="tablist"></ul>
          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-topline-red">
                    <div className="card-head">
                      <div className="tools">
                        <a
                          className="fa fa-repeat btn-color box-refresh"
                          onClick={handleRefresh}
                        ></a>
                        <a
                          className={
                            isOpenDisplay
                              ? 't-collapse btn-color fa fa-chevron-down'
                              : 't-collapse btn-color fa fa-chevron-up'
                          }
                          onClick={handleIsOpenDisplay}
                        ></a>
                        <a
                          className="t-close btn-color fa fa-times"
                          onClick={handleCloseTable}
                        ></a>
                      </div>
                    </div>
                    <div
                      className="card-body"
                      style={{ display: isOpenDisplay ? 'none' : 'block' }}
                    >
                      {renderNavControl()}
                      <Paper className={classes.root}>
                        {loading ? <AppLoading loading={true} /> : null}
                        <TableContainer className={classes.container}>
                          <Table stickyHeader>
                            <TableHead>{renderColumns()}</TableHead>
                            <TableBody>{renderTable()}</TableBody>
                          </Table>
                        </TableContainer>
                        {renderPagination()}
                      </Paper>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
