/* eslint-disable prettier/prettier */
import { createSelector } from '@reduxjs/toolkit';
import { RootState } from 'store/core/types';
import { initialState } from './slice';

const selectExamState = (state: RootState) => state.examination || initialState;

export const selectLoading = createSelector(
  [selectExamState],
  examState => examState.loading,
);

export const selectError = createSelector(
  [selectExamState],
  examState => examState.error,
);

export const selectSuccess = createSelector(
  [selectExamState],
  examState => examState.success,
);

export const selectExams = createSelector(
  [selectExamState],
  examState => examState.exams,
);
