/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable @typescript-eslint/no-unused-vars */
// eslint-disable react-hooks/exhaustive-deps
import React, { useState, useEffect } from 'react';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import * as _ from 'lodash';
import * as PatientSlice from 'store/patient/shared/slice';
import * as LocationSlice from 'store/location/shared/slice';
import * as fromPatientSelector from 'store/patient/shared/selectors';
import * as fromAuth from 'store/auth/shared/selectors';
import { useSelector, useDispatch } from 'react-redux';
import { PatientSaga } from 'store/patient/shared/saga';
import { LocationSaga } from 'store/location/shared/saga';
import { AppHelper, Order } from 'utils/app.helper';
import {
  DialogKey,
  DilogTitle,
  PatientStatus,
  ButtonToolTip,
} from 'store/patient/constants/http.constant';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import { ModalConfirm } from '../../../components/Modal/Confirm';
import { ModalPatient } from '../components/ModalPatient';
import { Tooltip } from '@material-ui/core';
import { useSnackbar } from 'notistack';
import { NotifyService } from 'services/notify.service';
import { RESPONSE_MESSAGE } from 'store/patient/constants/patient.constant';
import { useStyles } from 'store/common/constants/common.constant';
import { PatientService } from 'services/patient.service';
import { CurrencyService } from 'services/currency.service';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import { TekDatePicker } from 'app/components/InputDatePicker';
import {
  DEFAULT_FORMAT_DATE,
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { useForm, Controller } from 'react-hook-form';
import { Popconfirm } from 'antd';
import { makeStyles } from '@material-ui/core/styles';
import AppLoading from 'app/components/Loading';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import TableHeaderSort from 'app/components/TableHeaderSort';
import DateFnsUtils from '@date-io/date-fns';
import viLocale from 'date-fns/locale/vi';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import CheckBox from 'app/components/Checkbox';

const useStylesCheckBox = makeStyles({
  formLabel: {
    '& .MuiTypography-body1': {
      fontWeight: 'bold',
      fontSize: '1em',
    },
  },
});

export const patientCols = [
  { id: 'no', label: '#', minWidth: 50 },
  {
    id: 'actions',
    label: 'Chức năng',
    minWidth: 170,
  },
  {
    id: 'is_active',
    label: 'Trạng Thái',
    minWidth: 130,
    align: 'center',
  },
  {
    id: 'is_vip',
    label: 'Loại bệnh nhân',
    minWidth: 160,
    align: 'center',
  },
  { id: 'tekmedi_card_number', label: 'Mã thẻ', minWidth: 170 },
  { id: 'code', label: 'Mã bệnh nhân', minWidth: 200 },
  { id: 'full_name', label: 'Họ và tên', minWidth: 250 },
  {
    id: 'gender',
    label: 'Giới tính',
    minWidth: 130,
  },
  {
    id: 'birthday',
    label: 'Năm sinh',
    minWidth: 120,
  },
  {
    id: 'province_name',
    label: 'Tỉnh/TP',
    minWidth: 230,
  },
  {
    id: 'district_name',
    label: 'Quận/Huyện',
    minWidth: 200,
  },
  {
    id: 'ward_name',
    label: 'Phường/xã/thị trấn',
    minWidth: 200,
  },
  {
    id: 'identity_card_number',
    label: 'CMND',
    minWidth: 100,
  },
  {
    id: 'identity_card_number_issued_date',
    label: 'Ngày cấp',
    minWidth: 150,
  },
  {
    id: 'identity_card_number_issued_place',
    label: 'Nơi cấp',
    minWidth: 120,
  },
  {
    id: 'phone',
    label: 'Số điện thoại',
    minWidth: 150,
  },
  {
    id: 'top_up_amount',
    label: 'Số tiền đã nạp',
    minWidth: 170,
  },
  {
    id: 'hold_amount',
    label: 'Số tiền tạm ứng',
    minWidth: 170,
  },
  {
    id: 'finnaly_amount',
    label: 'Số Tiền Tất Toán',
    minWidth: 170,
  },
  {
    id: 'balance',
    label: 'Số tiền trong thẻ',
    minWidth: 170,
  },
  {
    id: 'health_insurance_number',
    label: 'Mã BHYT',
    minWidth: 170,
  },
  {
    id: 'health_insurance_first_place_code',
    label: 'Mã đơn vị',
    minWidth: 170,
  },
  {
    id: 'health_insurance_first_place',
    label: 'Tên đơn vị',
    minWidth: 170,
  },
  {
    id: 'health_insurance_issued_date',
    label: 'Ngày bắt đầu',
    minWidth: 170,
  },
  {
    id: 'health_insurance_expired_date',
    label: 'Ngày kết thúc',
    minWidth: 170,
  },
];

export const defaultPagi = {
  pageNumber: 1,
  pageSize: 10,
};

export const defaultDate = {
  startDate: new Date(),
  endDate: new Date(),
};

export function PatientPage() {
  const classesCheckbox = useStylesCheckBox();
  const classes = useStyles();
  useInjectReducer({
    key: PatientSlice.sliceKey,
    reducer: PatientSlice.reducer,
  });
  useInjectReducer({
    key: LocationSlice.sliceKey,
    reducer: LocationSlice.reducer,
  });
  useInjectSaga({
    key: PatientSlice.sliceKey,
    saga: PatientSaga,
  });
  useInjectSaga({
    key: LocationSlice.sliceKey,
    saga: LocationSaga,
  });
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const patients: any = useSelector(fromPatientSelector.selectDataListPatient);
  const loading: boolean = useSelector(fromPatientSelector.selectLoading);
  const users = useSelector(fromAuth.selectUser);
  const dispatch = useDispatch();
  const [openConfirmDelete, setOpenConfirmDelete] = useState(false);
  const [openConfirmChangeStatus, setOpenConfirmChangeStatus] = useState(false);
  const [modalMvip, setModalMvip] = React.useState<boolean>(false);
  const [openModal, setOpenModal] = useState<boolean>(false);
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<any>('tekmedi_card_number');
  const [selectPatient, setSelectPatient] = useState({});
  const { handleSubmit, register, control, setValue, getValues } = useForm({
    defaultValues: {
      ...defaultDate,
    },
  });
  const [pagi, setPagi] = useState<any>(defaultPagi);
  const [startDate, setStartDate] = useState<any>(defaultDate.startDate);
  const [endDate, setEndDate] = useState<any>(defaultDate.endDate);
  const [patientType, setPatientType] = React.useState('');
  const [patientId, setPatientId] = React.useState<string>('');

  useEffect(() => {
    dispatch(LocationSlice.actions.getLocationsAll());
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case PatientSlice.actions.findPatientWithIdSuccess.type:
          setOpenModal(true);
          break;
        case PatientSlice.actions.createPatientSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.CREATE_PATIENT_SUCCESS)
            .setSuccessNotify();
          renderPatient();
          break;
        case PatientSlice.actions.updatePatientSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.UPDATE_PATIENT_SUCCESS)
            .setSuccessNotify();
          setOpenModal(false);
          renderPatient();
          break;
        case PatientSlice.actions.changePatientVipSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.CHANGE_PATIENT_STATUS_SUCCESS)
            .setSuccessNotify();
          setPatientType('');
          setPatientId('');
          setModalMvip(false);
          break;
        case PatientSlice.actions.changePatientStatusSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.CHANGE_PATIENT_STATUS_SUCCESS)
            .setSuccessNotify();
          renderPatient();
          break;
        case PatientSlice.actions.syncPatientSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.SYNC_PATIENT_SUCCESS)
            .setSuccessNotify();
          break;
        case PatientSlice.actions.exportListPatientSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.EXPORT_PATIENT_SUCCESS)
            .setSuccessNotify();
          AppHelper.createDownloadFile({
            file_path: payload.file_path,
            file_name: payload.file_name,
          });
          break;
        case PatientSlice.actions.createPatientFailure.type:
        case PatientSlice.actions.updatePatientFailure.type:
        case PatientSlice.actions.changePatientStatusFailure.type:
        case PatientSlice.actions.exportListPatientFailure.type:
        case PatientSlice.actions.syncPatientFailure.type:
        case PatientSlice.actions.changePatientVipFailure.type:
          notifyController.setNotifyMessage(payload).setFailNotify();
          break;
        default:
          break;
      }
    });
    return () => {
      dispatch(PatientSlice.actions.resetAll());
      storeSub$();
    };
  }, []);

  const getFormValues = () => {
    return {
      search: getValues('search'),
      startDate: getValues('startDate'),
      endDate: getValues('endDate'),
      employeeName: users.full_name,
      employeeId: users.id,
      pageNumber: pagi.pageNumber,
      pageSize: pagi.pageSize,
    };
  };

  const renderPatient = () => {
    dispatch(PatientSlice.actions.getListPatient({ ...getFormValues() }));
  };

  const handleReset = () => {
    setValue('search', null);
    setValue('startDate', new Date());
    setValue('endDate', new Date());
    setStartDate(new Date());
    setEndDate(new Date());
    setPagi(defaultPagi);
    dispatch(
      PatientSlice.actions.getListPatient({
        ...getFormValues(),
        startDate: new Date(),
        endDate: new Date(),
        pageNumber: 1,
        pageSize: 10,
      }),
    );
  };

  const handleChangePage = (event: unknown, pageNumber: number) => {
    let newPagi = Object.assign({}, pagi, {
      pageNumber: pageNumber + 1,
    });
    setPagi(newPagi);
    dispatch(
      PatientSlice.actions.getListPatient({ ...getFormValues(), ...newPagi }),
    );
  };

  const handleChangeRowsPerPage = event => {
    let newPagi = Object.assign({}, pagi, {
      pageSize: +event.target.value,
      pageNumber: 1,
    });
    setPagi(newPagi);
    dispatch(
      PatientSlice.actions.getListPatient({ ...getFormValues(), ...newPagi }),
    );
  };

  const openDialogControl = (key, patient?) => {
    setSelectPatient(patient);
    switch (key) {
      case DialogKey.DELETE_PATIENT:
        setOpenConfirmDelete(true);
        break;
      case DialogKey.CHANGE_STATUS_PATIENT:
        setOpenConfirmChangeStatus(true);
        break;
    }
  };

  const closeDialogControl = key => {
    switch (key) {
      case DialogKey.DELETE_PATIENT:
        setOpenConfirmDelete(false);
        break;
      case DialogKey.CHANGE_STATUS_PATIENT:
        setOpenConfirmChangeStatus(false);
        break;
      case DialogKey.MVIP:
        setModalMvip(false);
        break;
    }
  };

  const deletePatient = () => {
    dispatch(PatientSlice.actions.deletePatient(_.get(selectPatient, 'id')));
  };

  const changePatientStatus = () => {
    const newPatientStatus = {
      id: _.get(selectPatient, 'id'),
      is_active: !_.get(selectPatient, 'is_active'),
    };
    dispatch(PatientSlice.actions.changePatientStatus(newPatientStatus));
  };

  const downloadPatient = () => {
    dispatch(PatientSlice.actions.exportListPatient({ ...getFormValues() }));
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const submitSearch = value => {
    dispatch(PatientSlice.actions.getListPatient({ ...getFormValues() }));
  };
  const changePatientVip = patient => {
    if (_.isEmpty(patientType)) {
      return notifyController
        .setNotifyMessage(RESPONSE_MESSAGE.CHECK_PATIENT_TYPE)
        .setFailNotify();
    }
    if (!_.isEmpty(patientType) && _.isEmpty(patient.tekmedi_card_number)) {
      return notifyController
        .setNotifyMessage(RESPONSE_MESSAGE.CHECK_PATIENT_TYPE_NOT_CARD)
        .setFailNotify();
    }
    if (
      !_.isEmpty(patientType) &&
      !_.isEmpty(patient.tekmedi_card_number) &&
      patientType === '3'
    ) {
      setPatientId(patient.id);
      return setModalMvip(true);
    }
    if (!_.isEmpty(patientType) && !_.isEmpty(patient.tekmedi_card_number)) {
      return dispatch(
        PatientSlice.actions.changePatientVip({
          ...patient,
          is_m_vip: false,
          is_vip: patientType === '2' ? true : false,
          id: patient.id,
          patient_type: patientType,
          employee_id: users.id,
        }),
      );
    }
  };

  const handleMVip = () => {
    if (!_.isEmpty(patientType)) {
      return dispatch(
        PatientSlice.actions.changePatientVip({
          is_m_vip: patientType === '3' ? true : false,
          is_vip: false,
          id: patientId,
          patient_type: patientType,
          employee_id: users.id,
        }),
      );
    }
  };
  const renderTableControl = () => {
    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={viLocale}>
        <form onSubmit={handleSubmit(submitSearch)}>
          <div className="row" style={{ justifyContent: 'space-between' }}>
            <div className="col-md-2">
              <label>Tìm bệnh nhân</label>
              <input
                className="form-control mr-4"
                ref={register()}
                placeholder="Tìm bệnh nhân"
                type="text"
                name="search"
              />
            </div>
            <div className="col-md-4 row">
              <div className="col-md-5">
                <label>Thời gian</label>
                <Controller
                  name="startDate"
                  control={control}
                  disableFuture
                  render={({ field, ...input }: any) => {
                    return (
                      <DatePicker
                        value={input.value}
                        format={DEFAULT_FORMAT_DATE}
                        TextFieldComponent={TekDatePicker}
                        placeholder="Ngày bắt đầu"
                        maxDate={endDate}
                        onChange={val => {
                          setValue('startDate', val);
                          setStartDate(val);
                        }}
                      />
                    );
                  }}
                />
              </div>
              <span className="span__line">-</span>
              <div className="col-md-5 mg-t">
                <Controller
                  name="endDate"
                  control={control}
                  defaultValue={null}
                  render={({ field, ...input }: any) => {
                    return (
                      <DatePicker
                        value={input.value}
                        format={DEFAULT_FORMAT_DATE}
                        TextFieldComponent={TekDatePicker}
                        placeholder="Ngày kết thúc"
                        minDate={startDate}
                        maxDate={new Date()}
                        onChange={val => {
                          setValue('startDate', val);
                          setEndDate(val);
                        }}
                      />
                    );
                  }}
                />
              </div>
            </div>
            <div className="col-md-2">
              <div className="btn-group">
                <button
                  className="btn btn-primary mr-3 btn__search"
                  type="submit"
                >
                  Tìm kiếm<i className="fa fa-search"></i>
                </button>
              </div>
            </div>
            <div className="col-md-4 export__examination text-right">
              <div className="btn-group mr-2">
                <button
                  type="button"
                  className="btn btn-warning btn__export"
                  onClick={handleReset}
                >
                  Reset <i className="fa fa-refresh"></i>
                </button>
              </div>
              <div className="btn-group">
                <button
                  aria-controls="simple-menu"
                  aria-haspopup="true"
                  type="button"
                  className="btn btn-success btn__export"
                  onClick={downloadPatient}
                >
                  Xuất file <i className="fa fa-download"></i>
                </button>
              </div>
              <div className="btn-group" hidden>
                <button
                  className="btn btn-primary edit__add"
                  onClick={() => openDialogControl(DialogKey.CREATE_PATIENT)}
                >
                  Thêm mới <i className="fa fa-plus"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
      </MuiPickersUtilsProvider>
    );
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={patientCols}
      />
    );
  };

  const renderTable = () => {
    if (!_.isEmpty(patients) && patients.data.length) {
      const tableData: any[] = AppHelper.stableSort(
        patients.data,
        AppHelper.getComparator(order, orderBy),
      );
      return tableData.map((patient, _id) => {
        return (
          <TableRow key={patient.id} hover>
            {patientCols.map(column => {
              const { page_number, page_size } = patients;
              const value = patient[column.id];
              const isMVip = patient['is_m_vip'];
              const isVip = patient['is_vip'];
              if (column.id === 'is_vip') {
                return (
                  <TableCell key={column.id} className={classes.td}>
                    <Popconfirm
                      title={
                        <React.Fragment>
                          <p>Bạn có muốn thay đổi loại bênh nhân này không?</p>
                          <RadioGroup
                            aria-label="gender"
                            name="gender1"
                            value={patientType}
                            onChange={e => setPatientType(e.target.value)}
                            style={{
                              flexDirection: 'row',
                            }}
                          >
                            {!isMVip && !isVip ? (
                              <React.Fragment>
                                <FormControlLabel
                                  className={classesCheckbox.formLabel}
                                  value="3"
                                  control={<CheckBox color="primary" />}
                                  label="MVIP"
                                />
                                <FormControlLabel
                                  className={classesCheckbox.formLabel}
                                  value="2"
                                  control={<CheckBox color="primary" />}
                                  label="VIP"
                                />
                              </React.Fragment>
                            ) : !isMVip && isVip ? (
                              <React.Fragment>
                                <FormControlLabel
                                  className={classesCheckbox.formLabel}
                                  value="3"
                                  control={<CheckBox color="primary" />}
                                  label="MVIP"
                                />
                                <FormControlLabel
                                  className={classesCheckbox.formLabel}
                                  value="1"
                                  control={<CheckBox color="primary" />}
                                  label="Thường"
                                />
                              </React.Fragment>
                            ) : isMVip && !isVip ? (
                              <FormControlLabel
                                className={classesCheckbox.formLabel}
                                value="1"
                                control={<CheckBox color="primary" />}
                                label="Thường"
                              />
                            ) : (
                              ''
                            )}
                          </RadioGroup>
                        </React.Fragment>
                      }
                      onConfirm={() => changePatientVip(patient)}
                      onCancel={() => setPatientType('')}
                      okText="Đồng ý"
                      cancelText="Hủy"
                    >
                      {(isMVip && !isVip) ||
                      (!isMVip && isVip) ||
                      (!isMVip && !isVip) ? (
                        <button
                          type="button"
                          className={`btn btn-xs ${
                            isMVip && !isVip
                              ? 'btn-success'
                              : !isMVip && isVip
                              ? 'btn-info'
                              : !isMVip && !isVip
                              ? 'btn-default'
                              : 'btn-default'
                          }`}
                          onClick={() => setPatientType('')}
                        >
                          {isMVip && !isVip
                            ? 'MVip'
                            : !isMVip && isVip
                            ? 'VIP'
                            : !isMVip && !isVip
                            ? 'Thường'
                            : ''}
                          <i className="fa fa-refresh"></i>
                        </button>
                      ) : (
                        '-'
                      )}
                    </Popconfirm>
                  </TableCell>
                );
              }
              if (column.id === 'no') {
                return (
                  <TableCell key={column.id} className={classes.td}>
                    {page_number === 1
                      ? page_number + _id
                      : _id + 1 + (page_number - 1) * page_size}
                  </TableCell>
                );
              }
              if (column.id === 'birthday') {
                return (
                  <TableCell key={column.id} className={classes.td}>
                    {value ? new PatientService().showBirthDay(patient) : '-'}
                  </TableCell>
                );
              }
              if (
                column.id === 'health_insurance_issued_date' ||
                column.id === 'health_insurance_expired_date' ||
                column.id === 'identity_card_number_issued_date'
              ) {
                return (
                  <TableCell key={column.id} className={classes.td}>
                    {value ? new PatientService().ortherDateFormat(value) : '-'}
                  </TableCell>
                );
              }
              if (
                column.id === 'payment_amount' ||
                column.id === 'top_up_amount' ||
                column.id === 'hold_amount' ||
                column.id === 'finnaly_amount' ||
                column.id === 'balance'
              ) {
                return (
                  <TableCell key={column.id} className={classes.td}>
                    {CurrencyService.formatCurrency(value)}
                  </TableCell>
                );
              }
              if (column.id === 'is_active') {
                return (
                  <TableCell key={column.id} className={classes.td}>
                    <Tooltip
                      title={
                        value ? PatientStatus.ACTIVE : PatientStatus.IN_ACTIVE
                      }
                      enterDelay={500}
                      leaveDelay={200}
                    >
                      <i
                        style={{
                          color: value ? '#4caf50' : '#ff0000',
                          fontSize: '16px',
                        }}
                        className={`fa fa-${value ? 'check' : 'times'}-circle`}
                        aria-hidden="true"
                      ></i>
                    </Tooltip>
                  </TableCell>
                );
              }
              if (column.id === 'actions') {
                return (
                  <TableCell key={column.id} className={classes.td}>
                    <Tooltip
                      title={ButtonToolTip.EDIT}
                      enterDelay={500}
                      leaveDelay={100}
                    >
                      <button
                        className="btn btn-primary btn-xs"
                        onClick={() =>
                          dispatch(
                            PatientSlice.actions.findPatientWithId(patient.id),
                          )
                        }
                      >
                        <i className="fa fa-pencil"></i>
                      </button>
                    </Tooltip>
                    <Tooltip title="Đồng bộ" enterDelay={500} leaveDelay={100}>
                      <button
                        className="btn btn-info btn-xs mx-2"
                        onClick={() => {
                          dispatch(
                            PatientSlice.actions.syncPatient(patient.id),
                          );
                        }}
                      >
                        <i className="fa fa-refresh"></i>
                      </button>
                    </Tooltip>
                    <Tooltip
                      title={ButtonToolTip.CHANGE_STATUS}
                      enterDelay={500}
                      leaveDelay={100}
                    >
                      <button
                        className={`btn btn-${
                          patient.is_active ? 'success' : 'danger'
                        } btn-xs mx-2`}
                        onClick={() =>
                          openDialogControl(
                            DialogKey.CHANGE_STATUS_PATIENT,
                            patient,
                          )
                        }
                      >
                        <i
                          className={`fa fa-${
                            patient.is_active ? 'check' : 'times'
                          }`}
                        ></i>
                      </button>
                    </Tooltip>
                  </TableCell>
                );
              }
              return (
                <TableCell key={column.id} className={classes.td}>
                  {value ? value : '-'}
                </TableCell>
              );
            })}
          </TableRow>
        );
      });
    } else {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={patientCols.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
  };

  const renderPagination = () => {
    if (!_.isEmpty(patients) && patients.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          labelRowsPerPage="Hiển Thị"
          count={patients.total_records}
          rowsPerPage={pagi.pageSize}
          page={pagi.pageNumber - 1}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      <div className="col-md-12">
        <div className="card card-topline-red">
          <div className="card-head">
            <div className="tools"></div>
          </div>
          <div className="card-body">
            {renderTableControl()}
            <Paper className={classes.root}>
              {loading ? <AppLoading loading={true} /> : null}
              <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table">
                  <TableHead>{renderColumns()}</TableHead>
                  <TableBody>{renderTable()}</TableBody>
                </Table>
              </TableContainer>
              {renderPagination()}
            </Paper>
            <ModalConfirm
              title={DilogTitle.DELETE_PATIENT}
              keyConfirm={DialogKey.DELETE_PATIENT}
              open={openConfirmDelete}
              onClose={() => closeDialogControl(DialogKey.DELETE_PATIENT)}
              confirmMethod={deletePatient}
            />
            <ModalConfirm
              title={DilogTitle.CHANGE_STATUS_PATIENT}
              keyConfirm={DialogKey.CHANGE_STATUS_PATIENT}
              open={openConfirmChangeStatus}
              onClose={() =>
                closeDialogControl(DialogKey.CHANGE_STATUS_PATIENT)
              }
              confirmMethod={changePatientStatus}
            />
            <ModalConfirm
              title={DilogTitle.CHANGE_STATUS_PATIENT}
              keyConfirm={DialogKey.MVIP}
              open={modalMvip}
              onClose={() => {
                closeDialogControl(DialogKey.MVIP);
              }}
              confirmMethod={handleMVip}
            />
            {openModal ? (
              <ModalPatient
                open={openModal}
                onClose={() => setOpenModal(false)}
              />
            ) : null}
          </div>
        </div>
      </div>
    </div>
  );
}
