/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable prettier/prettier */
import { createSelector } from '@reduxjs/toolkit';
import { RootState } from 'store/core/types';
import { initialState } from './slice';

const selectPaymentState = (state: RootState) => state.payments || initialState;

export const selectLoading = createSelector(
  [selectPaymentState],
  statePayment => statePayment.loading,
);

export const selectError = createSelector(
  [selectPaymentState],
  statePayment => statePayment.error,
);

export const selectSuccess = createSelector(
  [selectPaymentState],
  statePayment => statePayment.success,
);

export const selectPayments = createSelector(
  [selectPaymentState],
  statePayment => statePayment.payments
);

export const selectPaymentsPending = createSelector(
  [selectPaymentState],
  statePayment => statePayment.paymentsPending
);
