/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';
import * as _ from 'lodash';
import * as paymentSlice from 'store/payment/shared/slice';
import * as fromPayment from 'store/payment/shared/selectors';
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
  Tooltip,
} from '@material-ui/core';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import { useStyles } from 'store/common/constants/common.constant';
import { Order } from 'utils/app.helper';
import {
  DEFAULT_FORMAT_DATE,
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { useInjectReducer, useInjectSaga } from 'redux-injectors';
import { useDispatch, useSelector } from 'react-redux';
import { PaymentSaga } from 'store/payment/shared/saga';
import { AppHelper } from 'utils/app.helper';
import { useForm, Controller } from 'react-hook-form';
import { TekDatePicker } from 'app/components/InputDatePicker';
import { useSnackbar } from 'notistack';
import { NotifyService } from 'services/notify.service';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import { defaultPagi, defaultDate } from './Patient';
import { PatientService } from 'services/patient.service';
import { CurrencyService } from 'services/currency.service';
import { ModalSyncPaymentPending } from '../components/ModalSyncPaymentPending';
import TableHeaderSor from 'app/components/TableHeaderSort';
import AppLoading from 'app/components/Loading';
import DateFnsUtils from '@date-io/date-fns';
import viLocale from 'date-fns/locale/vi';

export const colsPayment = [
  { id: 'no', label: '#', minWidth: 100 },
  { id: 'patient_code', label: 'Mã bệnh nhân', minWidth: 200 },
  { id: 'full_name', label: 'Tên bệnh nhân', minWidth: 200 },
  { id: 'gender', label: 'Giới tính', minWidth: 200 },
  { id: 'birthday', label: 'Ngày sinh', minWidth: 200 },
  { id: 'registered_code', label: 'Mã biên lai', minWidth: 200 },
  { id: 'status', label: 'Trạng thái', minWidth: 200 },
  { id: 'amount', label: 'Số tiền', minWidth: 200 },
  { id: 'registered_date', label: 'Ngày đăng ký', minWidth: 200 },
];

const GET_PEDING_FAIL = 'Lấy danh sách thất bại';
const SYNC_FAIL = 'Đồng bộ danh sách bệnh nhân chưa tất toán thất bại';
const SYNC_SUCCESS = 'Đồng bộ danh sách bệnh nhân chưa tất toán thành công';

export function PatientPendingPayment() {
  useInjectReducer({
    key: paymentSlice.sliceKey,
    reducer: paymentSlice.reducer,
  });
  useInjectSaga({
    key: paymentSlice.sliceKey,
    saga: PaymentSaga,
  });
  const classes = useStyles();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const paymentPending: any = useSelector(fromPayment.selectPaymentsPending);
  const loading: any = useSelector(fromPayment.selectLoading);
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<any>('patient_code');
  const [pagi, setPagi] = useState<any>({});
  const [openSync, setOpenSync] = useState<boolean>(false);

  useEffect(() => {
    getPaymentsPending(defaultPagi, defaultDate);
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case paymentSlice.actions.syncPaymentsPendingSuccess.type:
          notifyController.setNotifyMessage(SYNC_SUCCESS).setSuccessNotify();
          setOpenSync(false);
          break;
        case paymentSlice.actions.getPaymentsPendingFail.type:
          notifyController
            .setNotifyMessage(payload ? payload : GET_PEDING_FAIL)
            .setFailNotify();
          break;
        case paymentSlice.actions.syncPaymentsPendingFail.type:
          notifyController
            .setNotifyMessage(payload ? payload : SYNC_FAIL)
            .setFailNotify();
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
    };
  }, []);

  const {
    handleSubmit,
    register,
    control,
    setValue,
    getValues,
    reset,
  } = useForm<any>({
    defaultValues: {
      ...defaultDate,
    },
  });

  const getFormValues = () => {
    return {
      search: getValues('search'),
      startDate: getValues('startDate'),
      endDate: getValues('endDate'),
    };
  };

  const resetFormValue = () => {
    setTimeout(() => {
      setValue('search', '');
      setValue('startDate', defaultDate.startDate);
      setValue('endDate', defaultDate.endDate);
    });
  };

  const getPaymentsPending = (newPagi: any, formSearch) => {
    setPagi(newPagi);
    dispatch(
      paymentSlice.actions.getPaymentsPending({
        ...newPagi,
        ...formSearch,
      }),
    );
  };

  const resetPatient = () => {
    getPaymentsPending(defaultPagi, defaultDate);
    reset();
    resetFormValue();
  };

  const handleRequestSort = (_e, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleChangePage = (e: unknown, pageNumber: number) => {
    const newPagi = {
      ...pagi,
      pageNumber: pageNumber + 1,
    };
    getPaymentsPending(newPagi, getFormValues());
  };

  const handleChangeRowPerPage = e => {
    const newPagi = {
      ...pagi,
      pageSize: +e.target.value,
      pageNumber: 1,
    };
    getPaymentsPending(newPagi, getFormValues());
  };

  const renderNavControl = () => {
    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={viLocale}>
        <form
          onSubmit={handleSubmit(values =>
            getPaymentsPending(defaultPagi, values),
          )}
        >
          <div className="row" style={{ justifyContent: 'space-between' }}>
            <div className="col-md-2">
              <label>Tìm bệnh nhân</label>
              <input
                className="form-control mr-4"
                ref={register()}
                placeholder="Tìm bệnh nhân"
                type="text"
                name="search"
              />
            </div>
            <div className="col-md-5 row">
              <div className="col-md-5">
                <label>Thời gian</label>
                <Controller
                  name="startDate"
                  control={control}
                  defaultValue={null}
                  render={({ field, ...input }: any) => {
                    return (
                      <DatePicker
                        value={input.value}
                        format={DEFAULT_FORMAT_DATE}
                        TextFieldComponent={TekDatePicker}
                        placeholder="Ngày bắt đầu"
                        onChange={val => {
                          setValue('startDate', val);
                        }}
                      />
                    );
                  }}
                />
              </div>
              <span className="span__line">-</span>
              <div className="col-md-5 mg-t">
                <Controller
                  name="endDate"
                  control={control}
                  defaultValue={null}
                  render={({ field, ...input }: any) => {
                    return (
                      <DatePicker
                        value={input.value}
                        format={DEFAULT_FORMAT_DATE}
                        TextFieldComponent={TekDatePicker}
                        placeholder="Ngày kết thúc"
                        onChange={val => {
                          setValue('endDate', val);
                        }}
                      />
                    );
                  }}
                />
              </div>
            </div>
            <div className="col-md-2">
              <div className="btn-group">
                <button
                  className="btn btn-primary mr-3 btn__search"
                  type="submit"
                >
                  Tìm kiếm <i className="fa fa-search"></i>
                </button>
              </div>
            </div>
            <div className="col-md-3 export__examination text-right">
              <div className="btn-group mr-2">
                <button
                  type="button"
                  className="btn btn-warning btn__export"
                  onClick={resetPatient}
                >
                  Reset <i className="fa fa-refresh"></i>
                </button>
              </div>
              <div className="btn-group">
                <button
                  type="button"
                  className="btn btn-success btn__export"
                  onClick={() => setOpenSync(true)}
                >
                  Đồng bộ <i className="fa fa-refresh"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
      </MuiPickersUtilsProvider>
    );
  };

  const renderColumns = () => (
    <TableHeaderSor
      classes={classes}
      onRequestSort={handleRequestSort}
      order={order}
      orderBy={orderBy}
      columns={colsPayment}
    />
  );

  const validateRender = () => {
    return (
      !_.isEmpty(paymentPending) &&
      !_.isEmpty(paymentPending.data) &&
      paymentPending.data.length
    );
  };

  const renderTable = () => {
    if (validateRender()) {
      const tableData: any[] = AppHelper.stableSort(
        paymentPending.data,
        AppHelper.getComparator(order, orderBy),
      );
      return tableData.map((row, index) => {
        return (
          <TableRow key={index} hover>
            {colsPayment.map(column => {
              const colId = column.id;
              const value = row[colId];
              if (colId === 'no') {
                const { page_number, page_size } = paymentPending;
                return (
                  <TableCell key={colId} className={classes.td}>
                    {page_number === 1
                      ? page_number + index
                      : index + 1 + (page_number - 1) * page_size}
                  </TableCell>
                );
              }
              if (colId === 'birthday') {
                return (
                  <TableCell key={colId} className={classes.td}>
                    {value ? new PatientService().showBirthDay(row) : '-'}
                  </TableCell>
                );
              }
              if (colId === 'registered_date') {
                return (
                  <TableCell key={colId} className={classes.td}>
                    {value ? new PatientService().ortherTimeFormat(value) : '-'}
                  </TableCell>
                );
              }
              if (colId === 'amount') {
                return (
                  <TableCell key={colId} className={classes.td}>
                    {CurrencyService.formatCurrency(value)}
                  </TableCell>
                );
              }
              return (
                <TableCell key={colId} className={classes.td}>
                  {value ? value : '-'}
                </TableCell>
              );
            })}
          </TableRow>
        );
      });
    }
    return (
      <TableRow>
        <TableCell
          className="w-100"
          align="center"
          colSpan={colsPayment.length}
        >
          {EMPTY_MESSAGE}
        </TableCell>
      </TableRow>
    );
  };

  const renderPagination = () => {
    if (validateRender()) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={paymentPending.total_records}
          rowsPerPage={paymentPending.page_size}
          page={paymentPending.page_number - 1}
          labelRowsPerPage="Hiển thị"
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">
          <ul className="nav customtab nav-tabs" role="tablist"></ul>
          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-topline-red">
                    <div className="card-head">
                      <div className="tools"></div>
                    </div>
                    <div className="card-body">
                      {renderNavControl()}
                      <Paper className={classes.root}>
                        {loading ? <AppLoading loading={true} /> : null}
                        <TableContainer className={classes.container}>
                          <Table stickyHeader aria-label="sticky table">
                            <TableHead>{renderColumns()}</TableHead>
                            <TableBody>{renderTable()}</TableBody>
                          </Table>
                        </TableContainer>
                        {renderPagination()}
                      </Paper>
                    </div>
                    {openSync && (
                      <ModalSyncPaymentPending
                        title="Đồng bộ danh sách bệnh nhân chưa tất toán"
                        open={openSync}
                        onClose={() => setOpenSync(false)}
                      />
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
