import { call, put, takeLatest, all } from 'redux-saga/effects';
import { actions } from './slice';
import { AutoPaymentHttp } from '../services/autopayment.http';
import { configResponse } from 'utils/request';
import * as _ from 'lodash';

const configRequest = new AutoPaymentHttp();

export function* updateTime(api, action) {
  const response = yield call(api.updateTime, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.updateTimeSuccess(data));
  } catch (error) {
    yield put(actions.updateTimeFailure(_.get(error, 'message')));
  }
}

export function* renderUpdateTime(api, action) {
  const response = yield call(api.renderUpdateTime, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.renderUpdateTimeSuccess(data));
  } catch (error) {
    yield put(actions.renderUpdateTimeFailure(_.get(error, 'message')));
  }
}

export function* paidWaittingHistories(api, action) {
  const response = yield call(api.paidWaittingHistories, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.paidWaittingHistoriesSuccess(data));
  } catch (error) {
    yield put(actions.paidWaittingHistoriesFailure(_.get(error, 'message')));
  }
}

export function* exportWaittingHistories(api, action) {
  const response = yield call(api.exportWaittingHistories, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.exportWaittingHistoriesSuccess(data));
  } catch (error) {
    yield put(actions.exportWaittingHistoriesFailure(_.get(error, 'message')));
  }
}

export function* AutoPaymentSaga() {
  yield all([
    yield takeLatest(actions.updateTime.type, updateTime, configRequest),
    yield takeLatest(
      actions.renderUpdateTime.type,
      renderUpdateTime,
      configRequest,
    ),
    yield takeLatest(
      actions.paidWaittingHistories.type,
      paidWaittingHistories,
      configRequest,
    ),
    yield takeLatest(
      actions.exportWaittingHistories.type,
      exportWaittingHistories,
      configRequest,
    ),
  ]);
}
