/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect, useMemo, useReducer } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import * as _ from 'lodash';
import * as AutoPaymentSelector from 'store/autopayment/shared/selectors';
import * as TransactionSelector from 'store/transaction/shared/selectors';
import * as AutoPaymentSlice from 'store/autopayment/shared/slice';
import * as TransactionSlice from 'store/transaction/shared/slice';
import * as AutoPaymentConts from 'store/autopayment/constants/autopayment.constant';
import * as fromAuth from 'store/auth/shared/selectors';
import { AutoPaymentSaga } from 'store/autopayment/shared/saga';
import { PaidWaitingFormSaga } from 'store/transaction/shared/saga';
import { makeStyles } from '@material-ui/core/styles';
import { TekDatePicker } from 'app/components/InputDatePicker';
import { Link } from 'react-router-dom';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import { NotifyService } from 'services/notify.service';
import { useSnackbar } from 'notistack';
import { AppHelper, Order } from 'utils/app.helper';
import { useForm, Controller } from 'react-hook-form';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import {
  DEFAULT_FORMAT_DATE,
  DEFAULT_FORMAT_DATE_MOMENT,
  DEFAULT_FORMAT_DATE_TIME_MOMENT,
  DEFAULT_FORMAT_INPUT_DATE,
  EMPTY_MESSAGE,
  FORMAT_TIME,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import ModalTransactionDetail from 'app/containers/ReportPage/components/ModalTransactionDetail';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import DateFnsUtils from '@date-io/date-fns';
import AppLoading from 'app/components/Loading';
import TableHeaderSort from 'app/components/TableHeaderSort';
import moment from 'moment';
import viLocale from 'date-fns/locale/vi';
import { TimePicker } from 'antd';
import 'antd/dist/antd.css';

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  table: {
    width: '100%',
    minWidth: '1600px',
  },
  container: {
    height: '100%',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
});

export const tablePayment = [
  { id: 'transaction_info_id', label: 'Mã giao dịch', minWidth: 100 },
  { id: 'registered_code', label: 'Số tiếp nhận', minWidth: 120 },
  { id: 'tekmedi_card_number', label: 'Mã thẻ', minWidth: 120 },
  { id: 'patient_code', label: 'Mã bệnh nhân', minWidth: 150 },
  { id: 'patient_name', label: 'Họ và tên', minWidth: 150 },
  {
    id: 'gender',
    label: 'Giới tính',
    minWidth: 100,
  },
  {
    id: 'birth_day',
    label: 'Ngày sinh',
    minWidth: 150,
  },
  {
    id: 'amount',
    label: 'Số tiền',
    minWidth: 150,
  },
  {
    id: 'auto_date',
    label: 'Ngày giờ',
    minWidth: 150,
  },
  {
    id: 'status_auto_payment',
    label: 'Trạng thái thực hiện',
    minWidth: 150,
  },
  {
    id: 'message',
    label: 'Chi tiết',
    minWidth: 200,
  },
];

const status = [
  { code: ' ', label: 'Tất cả' },
  { code: '2', label: 'Thành công' },
  { code: '3', label: 'Thất bại' },
];

interface ModalState {
  openModal: boolean;
}

enum ModalType {
  OPEN = 'OPEN',
}

type ModalActions = {
  type: ModalType;
  payload: any;
};

const initModalState: ModalState = {
  openModal: false,
};

const reduceModal = (
  state: ModalState = initModalState,
  { type, payload }: ModalActions,
): ModalState => {
  switch (type) {
    case ModalType.OPEN:
      return {
        ...state,
        openModal: payload,
      };
    default:
      return state;
  }
};

const defaultPagi = {
  page_size: 10,
  page_number: 0,
};

const defaultDate = {
  startDate: new Date().setDate(new Date().getDate() - 1),
  maxDay: new Date(),
};

export function AutoPayment() {
  const classes = useStyles();
  const dateUtil = new DateFnsUtils();
  const dispatch = useDispatch();
  useInjectReducer({
    key: AutoPaymentSlice.sliceKey,
    reducer: AutoPaymentSlice.reducer,
  });
  useInjectSaga({ key: AutoPaymentSlice.sliceKey, saga: AutoPaymentSaga });
  useInjectReducer({
    key: TransactionSlice.sliceKey,
    reducer: TransactionSlice.reducer,
  });
  useInjectSaga({ key: TransactionSlice.sliceKey, saga: PaidWaitingFormSaga });
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const [{ openModal }, dispatchModal] = useReducer(
    reduceModal,
    initModalState,
  );
  const { handleSubmit, register, control, setValue, getValues } = useForm({
    defaultValues: {
      ...defaultDate,
    },
  });
  const user = useSelector(fromAuth.selectUser);
  const loading = useSelector(AutoPaymentSelector.selectLoading);
  const loadingDetail = useSelector(TransactionSelector.selectLoading);
  const histories = useSelector(AutoPaymentSelector.selectHistories);
  const logUpdateTime = useSelector(AutoPaymentSelector.selectLogUpdateTime);
  const [datePay, setDatePay] = useState<any>(defaultDate.startDate);
  const [pagi, setPagi] = useState<any>(defaultPagi);
  const [order, setOrder] = React.useState<Order>('asc');
  const [orderBy, setOrderBy] = React.useState<any>('name');
  const [endTime, setEndTime] = React.useState<any>(null);
  const [startTime, setStartTime] = React.useState<any>(null);
  const [errStartTime, setErrStartTime] = React.useState<boolean>(false);
  const [errEndTime, setErrEndTime] = React.useState<boolean>(false);
  const [openDetail, setOpenDetail] = React.useState<boolean>(false);

  useEffect(() => {
    dispatch(AutoPaymentSlice.actions.renderUpdateTime({ ...getFormValues() }));
    dispatch(
      AutoPaymentSlice.actions.paidWaittingHistories({ ...getFormValues() }),
    );
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case AutoPaymentSlice.actions.updateTimeSuccess.type:
          dispatch(
            AutoPaymentSlice.actions.renderUpdateTime({ ...getFormValues() }),
          );
          notifyController
            .setNotifyMessage(
              AutoPaymentConts.RESPONSE_AUTO_PAY_MESSAGE.UPDATE_TIME_SUCCESS,
            )
            .setSuccessNotify();
          break;
        case AutoPaymentSlice.actions.paidWaittingHistoriesSuccess.type:
          notifyController
            .setNotifyMessage(
              AutoPaymentConts.RESPONSE_AUTO_PAY_MESSAGE
                .PAID_WAITTING_HISTORIES_SUCCESS,
            )
            .setSuccessNotify();
          break;
        case AutoPaymentSlice.actions.exportWaittingHistoriesSuccess.type:
          AppHelper.createDownloadFileNew({
            file_path: payload.file_path,
            file_name: payload.file_name,
          });
          break;
        case AutoPaymentSlice.actions.updateTimeFailure.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : AutoPaymentConts.RESPONSE_AUTO_PAY_MESSAGE.UPDATE_TIME_FAIL,
            )
            .setFailNotify();
          break;
        case AutoPaymentSlice.actions.paidWaittingHistoriesFailure.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : AutoPaymentConts.RESPONSE_AUTO_PAY_MESSAGE
                    .PAID_WAITTING_HISTORIES_FAIL,
            )
            .setFailNotify();
          break;
        case AutoPaymentSlice.actions.exportWaittingHistoriesFailure.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : AutoPaymentConts.RESPONSE_AUTO_PAY_MESSAGE
                    .EXPORT_WAITTING_HISTORIES_FAIL,
            )
            .setFailNotify();
          break;
        case TransactionSlice.actions.getListTransactionDetailModalSuccess.type:
          setOpenDetail(true);
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
    };
  }, []);

  const getFormValues = (): any => {
    return {
      date: dateUtil.format(datePay, DEFAULT_FORMAT_INPUT_DATE),
      status: getValues('status'),
      employee_name: !_.isEmpty(user) ? user.full_name : '',
      page_size: pagi.page_size,
      page_number: pagi.page_number === 0 ? 1 : pagi.page_number,
    };
  };

  const saveConfig = () => {
    const data = {
      start_Time: _.isEmpty(startTime) ? renderLogTime.start_time : startTime,
      end_Time: _.isEmpty(endTime) ? renderLogTime.end_time : endTime,
    };
    if (!errStartTime && !errEndTime) {
      dispatch(AutoPaymentSlice.actions.updateTime(data));
    }
  };

  const submitSearch = value => {
    dispatch(
      AutoPaymentSlice.actions.paidWaittingHistories({ ...getFormValues() }),
    );
  };

  const exportFile = () => {
    dispatch(
      AutoPaymentSlice.actions.exportWaittingHistories({ ...getFormValues() }),
    );
  };

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleChangePage = (event, page) => {
    let changePage = Object.assign({}, pagi, {
      page_number: page * pagi.page_size,
    });
    const request = {
      ...getFormValues(),
      page_size: pagi.page_size,
      page_number: ++page,
    };
    setPagi(changePage);
    dispatch(AutoPaymentSlice.actions.paidWaittingHistories(request));
  };

  const handleChangeRowsPerPage = e => {
    let changeRowPerPage = Object.assign({}, pagi, {
      page_size: +e.target.value,
      page_number: 0,
    });
    const request = {
      ...getFormValues(),
      ...changeRowPerPage,
    };
    setPagi(changeRowPerPage);
    dispatch(AutoPaymentSlice.actions.paidWaittingHistories(request));
  };

  const renderLogTime: any = useMemo(() => {
    if (!_.isEmpty(logUpdateTime)) {
      const { data } = logUpdateTime;
      return {
        start_time: _.get(data, 'config_time.start_time') || '',
        end_time: _.get(data, 'config_time.end_time') || '',
        updated_date: _.get(data, 'config_time.updated_date') || '',
      };
    }
  }, [logUpdateTime]);

  const renderData: any = useMemo(() => {
    if (!_.isEmpty(histories)) {
      const { total_records = 0, data } = histories;
      const { page_number, page_size } = pagi;
      return {
        page_number,
        page_size,
        total_records,
        paid_waiting_historys: _.get(data, 'paid_waiting_historys') || [],
      };
    }
  }, [histories]);

  const getStartTime = (value: any, time: any) => {
    if (_.isEmpty(time)) {
      setErrStartTime(true);
    }
    if (!_.isEmpty(time)) {
      setErrStartTime(false);
      setStartTime(time);
    }
  };

  const getEndTime = (value: any, time: any) => {
    if (_.isEmpty(time)) {
      setErrEndTime(true);
    }
    if (!_.isEmpty(time)) {
      setErrEndTime(false);
      setEndTime(time);
    }
  };

  const renderTableControl = () => {
    return (
      <div className="card card-box">
        <div className="card-body">
          <div className="card card-topline-aqua">
            <div className="card-body">
              <MuiPickersUtilsProvider utils={DateFnsUtils} locale={viLocale}>
                <form onSubmit={handleSubmit(submitSearch)}>
                  <h4 className="font-weight-bold">
                    CẤU HÌNH THỜI GIAN THỰC HIỆN TẤT TOÁN TỰ ĐỘNG
                  </h4>
                  {!_.isEmpty(renderLogTime) ? (
                    <div className="row">
                      <div className="col-md-4">
                        <label className="font-weight-bold">
                          Giờ bắt đầu tất toán tự động
                        </label>
                        <TimePicker
                          defaultValue={moment(
                            renderLogTime.start_time,
                            FORMAT_TIME,
                          )}
                          format={FORMAT_TIME}
                          onChange={getStartTime}
                          style={{
                            width: '100%',
                            height: '39px',
                            marginTop: '5px',
                          }}
                        />
                        {errStartTime ? (
                          <span className="style_error">
                            Vui lòng chọn giờ bắt đầu
                          </span>
                        ) : null}
                      </div>
                      <div className="col-md-4">
                        <label className="font-weight-bold">
                          Giờ kết thúc tất toán tự động
                        </label>
                        <TimePicker
                          defaultValue={moment(
                            renderLogTime.end_time,
                            FORMAT_TIME,
                          )}
                          format={FORMAT_TIME}
                          onChange={getEndTime}
                          style={{
                            width: '100%',
                            height: '39px',
                            marginTop: '5px',
                          }}
                        />
                        {errEndTime ? (
                          <span className="style_error">
                            Vui lòng chọn giờ kết thúc
                          </span>
                        ) : null}
                      </div>
                      <div className="col-md-4">
                        <div className="btn-group d-flex align-items-end">
                          <button
                            className="btn btn-success mg-t height37 mr-2"
                            type="button"
                            onClick={saveConfig}
                          >
                            Lưu
                          </button>
                          <div className="form-group">
                            <span className="style_update">
                              Lần cuối chỉnh sửa :{' '}
                              {!_.isEmpty(renderLogTime)
                                ? moment(renderLogTime.updated_date).format(
                                    'HH:mm  DD/MM/YYYY',
                                  )
                                : '-'}
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                  ) : null}

                  <hr className="w-100" />
                  <h4 className="font-weight-bold">
                    LỊCH SỬ THỰC HIỆN TẤT TOÁN TỰ ĐỘNG
                  </h4>
                  <div className="row">
                    <div className="col-md-4">
                      <label className="font-weight-bold">
                        Ngày thực hiện tất toán
                      </label>
                      <Controller
                        name="startDate"
                        control={control}
                        disableFuture
                        render={({ field, ...input }: any) => {
                          return (
                            <DatePicker
                              value={input.value}
                              format={DEFAULT_FORMAT_DATE}
                              TextFieldComponent={TekDatePicker}
                              placeholder="Ngày bắt đầu"
                              maxDate={defaultDate.maxDay}
                              onChange={val => {
                                setValue('startDate', val);
                                setDatePay(val);
                              }}
                            />
                          );
                        }}
                      />
                    </div>
                    <div className="col-md-4">
                      <label htmlFor="status" className="font-weight-bold">
                        Trạng thái
                      </label>
                      <select
                        name="status"
                        className="form-control height37"
                        ref={register}
                        defaultValue=" "
                      >
                        {status.map(({ code, label }) => {
                          return (
                            <option value={code} key={code}>
                              {label}
                            </option>
                          );
                        })}
                      </select>
                    </div>
                    <div className="col-md-4">
                      <div className="btn-group mg-t20 mr-1">
                        <button
                          className="btn btn-primary height37"
                          type="submit"
                        >
                          Tìm kiếm <i className="fa fa-search"></i>
                        </button>
                      </div>
                      <div className="btn-group mg-t20">
                        <button
                          className="btn btn-danger height37"
                          type="button"
                          onClick={exportFile}
                        >
                          Xuất file <i className="fa fa-upload"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </MuiPickersUtilsProvider>
            </div>
          </div>
        </div>
      </div>
    );
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={tablePayment}
      />
    );
  };

  const renderTable = () => {
    if (_.isEmpty(renderData) || !renderData.paid_waiting_historys.length) {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={tablePayment.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
    const tableData: any[] = AppHelper.stableSort(
      renderData.paid_waiting_historys,
      AppHelper.getComparator(order, orderBy),
    );
    return tableData.map((row, idx) => {
      const payment = _.cloneDeep(row);
      return (
        <TableRow key={idx}>
          {tablePayment.map(column => {
            const value = payment[column.id];
            if (column.id === 'transaction_info_id') {
              const { transaction_info_id, registered_code } = payment;
              return (
                <TableCell key={column.id}>
                  {payment['status_auto_payment'] === 2 && registered_code ? (
                    <button
                      className="btn btn-info btn-xs"
                      onClick={() => {
                        dispatch(
                          TransactionSlice.actions.getListTransactionDetailModal(
                            transaction_info_id,
                          ),
                        );
                      }}
                    >
                      {value}
                    </button>
                  ) : (
                    ''
                  )}
                </TableCell>
              );
            }
            if (column.id === 'registered_code') {
              return (
                <TableCell key={column.id}>
                  <Link
                    className="btn btn-info btn-xs"
                    to={{
                      pathname: '/transaction/detail',
                      search: `?patientCode=${row.patient_code}&registeredCode=${row.registered_code}&registeredDate=${row.auto_date}`,
                    }}
                    target="_blank"
                  >
                    {value}
                  </Link>
                </TableCell>
              );
            }
            if (column.id === 'gender') {
              return (
                <TableCell key={column.id}>
                  {value === 0 ? 'Nữ' : 'Nam'}
                </TableCell>
              );
            }
            if (column.id === 'birth_day') {
              return (
                <TableCell key={column.id}>
                  {moment(value).format(DEFAULT_FORMAT_DATE_MOMENT)}
                </TableCell>
              );
            }
            if (column.id === 'auto_date') {
              return (
                <TableCell key={column.id}>
                  {moment(value).format(DEFAULT_FORMAT_DATE_TIME_MOMENT)}
                </TableCell>
              );
            }
            if (column.id === 'amount') {
              return (
                <TableCell key={column.id}>
                  {AppHelper.formatCurrency(value)}
                </TableCell>
              );
            }
            if (column.id === 'status_auto_payment') {
              return (
                <TableCell key={column.id}>
                  {value === 0
                    ? 'Mới'
                    : value === 1
                    ? 'Đang xử lý'
                    : value === 2
                    ? 'Thành công'
                    : 'Thất bại'}
                </TableCell>
              );
            }
            return <TableCell key={column.id}>{value ? value : '-'}</TableCell>;
          })}
        </TableRow>
      );
    });
  };

  const renderPagination = () => {
    if (!_.isEmpty(renderData) && renderData.paid_waiting_historys.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={renderData.total_records}
          rowsPerPage={renderData.page_size}
          page={renderData.page_number / renderData.page_size}
          labelRowsPerPage={'Hiển Thị'}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      {loading && <AppLoading loading={true} />}
      {loadingDetail && <AppLoading loading={true} />}
      {openDetail && (
        <ModalTransactionDetail
          open={true}
          onClose={() => setOpenDetail(false)}
        />
      )}
      <div className="col-12">
        <div className="card card-topline-red">
          <div className="card-body">
            {renderTableControl()}
            <Paper className={classes.root}>
              <TableContainer className={classes.container}>
                <Table className={classes.table}>
                  <TableHead>{renderColumns()}</TableHead>
                  <TableBody>{renderTable()}</TableBody>
                </Table>
              </TableContainer>
              {renderPagination()}
            </Paper>
          </div>
        </div>
      </div>
    </div>
  );
}
