import { createSlice } from 'store/core/@reduxjs/toolkit';
export interface PaymentState {
  error?: any | null;
  loading: boolean;
  success: any;
  payments: any;
  paymentsPending: any;
}

export const initialState: PaymentState = {
  loading: false,
  error: null,
  success: {},
  payments: {},
  paymentsPending: {},
};

const PaymentSlice = createSlice({
  name: 'payments',
  initialState,
  reducers: {
    getPayments(state, action) {
      state.loading = true;
    },

    getPaymentsSuccess(state, action) {
      state.loading = false;
      state.payments = action.payload;
    },

    getPaymentsFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getPaymentsPending(state, action) {
      state.loading = true;
    },

    getPaymentsPendingSuccess(state, action) {
      state.loading = false;
      state.paymentsPending = action.payload;
    },

    getPaymentsPendingFail(state, action) {
      state.loading = false;
    },

    syncPaymentsPending(state, action) {
      state.loading = true;
    },

    syncPaymentsPendingSuccess(state, action) {
      state.loading = false;
    },

    syncPaymentsPendingFail(state, action) {
      state.loading = false;
    },
  },
});

export const { actions, reducer, name: sliceKey } = PaymentSlice;
